import React, { PureComponent } from 'react';
import { Box, Row } from "../../components/Layout/index";
import { Grid } from "../Layout/index";

const isTrinquet = (itemId) =>
    itemId === 3340 ||
    itemId === 3363 ||
    itemId === 3364;

const isWardOrPotion = (itemId) =>
    itemId === 2055 ||
    itemId === 2138 ||
    itemId === 2139 ||
    itemId === 2140;

const getItemsAndTrinket = (itemsId) => itemsId.reduce((acum, itemId) => {
    if (isTrinquet(itemId)) {
        acum.trinket = itemId;
    } else if (!isWardOrPotion(itemId)) {
        acum.items.push(itemId);
    }
    return acum;
}, { items: [], trinket: undefined })

export class Items extends PureComponent {
    render() {
        const { itemIds = [] } = this.props;
        const object = getItemsAndTrinket(itemIds);

        return (
            <Row>
                <Grid columns="repeat(3, 38px)" rows="38px" columnsGap="4px" rowsGap="4px" b="1px">
                    {object.items.map((itemId, index) => <Box key={index} url={`https://ddragon.leagueoflegends.com/cdn/8.12.1/img/item/${itemId}.png`} size="38px" backgroundSize="cover" />)}
                </Grid>
                <Box ml="4px" url={`https://ddragon.leagueoflegends.com/cdn/8.12.1/img/item/${object.trinket}.png`} size="38px" backgroundSize="cover" />
            </Row>
        );
    }
}



